'use client'

import { Quantico } from 'next/font/google'
import {
    Table,
    TableBody,
    TableCaption,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table"
const quantico = Quantico({
    weight: ['400', '700'],
    subsets: ['latin'],
    display: 'swap',
    variable: '--font-quantico',
})

export default function Page() {
    return (
        <div className='flex flex-col p-15'>
            <div className='w-full'>
                <h2 className={`text-5xl text-white text-shadow-accent-foreground text-center font-bold ${quantico.className}`}>
                    TRACK RECORDS
                </h2>
            </div>
            <div className='flex flex-row'>
                <div className=''>
                    Tab
                </div>
            </div>
        </div>
    )
}
